body {
  color: #fff;
  background: linear-gradient(135deg, #1e3c72, #2a5298);
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  display: flex;
}

.container {
  text-align: center;
  backdrop-filter: blur(10px);
  background: #ffffff1a;
  border-radius: 10px;
  width: 80%;
  max-width: 800px;
  margin: 2rem auto;
  padding: 2rem;
  box-shadow: 0 4px 30px #0000001a;
}

#image-container {
  border: 2px solid #fff;
  border-radius: 8px;
  margin: 2rem 0;
  position: relative;
  overflow: hidden;
}

#selection-image {
  cursor: crosshair;
  width: 100%;
  height: auto;
}

#coordinates-display {
  margin: 1rem 0;
  font-size: 1.2rem;
}

#next-button {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 4px;
  padding: .8rem 2rem;
  font-size: 1rem;
}

#next-button:disabled {
  cursor: not-allowed;
  background-color: #666;
}

h1 {
  text-align: center;
  margin-bottom: 1.5rem;
}

button {
  color: #fff;
  cursor: pointer;
  background: #4caf50;
  border: none;
  border-radius: 5px;
  width: 100%;
  margin: .5rem 0;
  padding: .75rem 1.5rem;
  font-size: 1rem;
  transition: all .3s;
}

button:hover {
  background: #45a049;
  transform: translateY(-2px);
}

form {
  flex-direction: column;
  display: flex;
}

label {
  margin-bottom: .5rem;
}

input {
  background: #fffc;
  border: none;
  border-radius: 5px;
  margin-bottom: 1rem;
  padding: .75rem;
  transition: all .3s;
}

input:focus {
  background: #fff;
  outline: none;
  box-shadow: 0 0 5px #ffffff80;
}

a {
  color: inherit;
  width: 100%;
  text-decoration: none;
  display: block;
}
